import { MenuItem, OutlinedInput, Select, Stack } from '@mui/material'
import { ACCOUNT_API } from 'API/account'
import DialogConfirm from 'components/DialogConfirm'
import { useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { UIAction } from 'redux/slices/UI'

const ITEM_HEIGHT = 48
const ITEM_PADDING_TOP = 8
const MenuProps = {
	PaperProps: {
		style: {
			maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
			width: 250,
		},
	},
}
export default function ChangeGroup() {
	const { gId, id } = useParams<{
		gId: string
		id: string
	}>()
	const nav = useNavigate()
	const dispatch = useAppDispatch()
	const groups = useAppSelector((s) => s.Group.list)
	const account = groups.find((f) => f.id === parseInt(gId || ''))?.accounts.find((f) => f.id === parseInt(id || ''))

	const [loading, setLoading] = useState(false)
	const [groupId, setGroupId] = useState(0)

	useEffect(() => {
		if (gId && id && !account) {
			nav(-1)
		}
	}, [account, gId, id, nav])

	const onSubmit = async () => {
		try {
			setLoading(true)
			const { error, message } = await ACCOUNT_API.updateGroup(id, groupId)
			dispatch(
				UIAction.addGlobalMessage({
					type: error ? 'error' : 'success',
					message,
				})
			)
		} finally {
			setLoading(false)
		}
	}

	if (!id || !gId || !account) return null

	return (
		<DialogConfirm
			open
			loading={loading}
			handleClose={() => nav(-1)}
			title="グループ変更"
			disabled={groupId === 0}
			handleSubmit={onSubmit}
			primaryButtonText="保存"
		>
			<Stack my={2}>
				<Select
					value={groupId}
					onChange={(e) => {
						const { value } = e.target
						if (value) {
							setGroupId(parseInt(`${value}`))
						}
					}}
					fullWidth
					input={<OutlinedInput fullWidth size="small" />}
					MenuProps={MenuProps}
				>
					<MenuItem disabled value={0}>
						選択してください
					</MenuItem>
					{groups.map((group) => (
						<MenuItem key={group.id} disabled={group.id === parseInt(gId)} value={group.id}>
							{group.name}
						</MenuItem>
					))}
				</Select>
			</Stack>
		</DialogConfirm>
	)
}
